.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  background-image: url('./turf.jpg');
  background-size: cover;
}

.App-header div {
    height: 20%;
    width: 40%;
    background-color: white;
    box-shadow: 1px 1px 11px #18171cb3;
    border-radius: 5px;
}

.App-header img {
    height: 200px;
    width: 200px;
}

.App-header p {
    font-weight: bold;
}

.App-header p:first-child {
    margin-bottom: 5px;
}

.App-header p:nth-child(2) {
    margin-top: 0;
    font-size: 1.1rem;
}

.App-link {
  color: #61dafb;
}

@media screen and (max-width: 400px) {
  .App-header img {
    height: 100px;
    width: 100px;
  }

  .App-header p:nth-child(2) {
    font-size: 0.8rem;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
